<template>
    <div class="hello">
        <h1>{{ msg }}</h1>
        <h1>当前地址：{{ address1 }}</h1>
        <h2>合约地址：{{ _contract }}</h2>
        <h2>web3版本：{{web3Version}}</h2>
        <h1>合约：{{ contract1 }}</h1>
        <h3>
            <p>余额：{{ balance }}</p>
        </h3>
        <h2>是否连接合约：{{1}}</h2>
        <input type="text" name="" id="" v-model="getAddress" />
        <button @click="getBalance">查询</button>
        <p>
            <button >连接智能合约</button>
        </p>
        <p><button @click="contractMathod">调用合约函数</button></p>
        <p><button @click="createAccount">创建账户</button></p>
        <h1>账户：{{newAddress}}</h1>
        <h2>私钥：{{newprivatekey}}</h2>
    </div>
</template>

<script>
import { ref, computed, reactive } from "vue";
import abi from "./abi.json";
// // console.log(abi);
// // import {ethers} from 'ethers';
import Web3 from "web3";

const web3 = new Web3("HTTP://127.0.0.1:7545");
const eth =web3.eth;
const _contract = "0x985e7965a4b6296a2d412C64e401CD3c25ed18b6";

export default {
    name: "HelloWorld",
    data() {
        return {
            getAddress: "",
            balance: "",
            address1: "",
            _contract,
            abi,
            web3Version:web3.version,
            // isConnetContract:false,
            newAddress:'',newprivatekey:'',
        };
    },
    async setup() {



      //连接到智能合约
        let contract1 = await new web3.eth.Contract(abi, _contract);
      
        
        console.log(contract1);
  
        

  
      //调用合约方法函数，点击按钮调用
       async function contractMathod() {
            
            await contract1.methods._owner().call((err,res)=>console.log(res))}
        // }

        return {contractMathod};
    },
    methods: {

      // 获取输入框地址的余额 , 如果地址无效，返回无效地址
        async getBalance() {
          if(!web3.utils.isAddress(this.getAddress)){
            this.balance = '无效地址'
          }else{
            this.address1 = this.getAddress;
            this.balance =
                (await web3.eth.getBalance(this.getAddress, (err, wei) => {
                    var balance = web3.utils.fromWei(wei, "ether");
                    return balance;
                })) + "wei";
          }
            

        },
        // -----
        // async connetContract() {},

        // async contractMathod() {
        //     console.log(this.contract);
        //     //  await this.contract.methods._owner().call((err, result) => { console.log(result) })
        // },
        // create_account(){
        //  let a =  eth.accounts.create()
        //   console.log(a);
        // }

        async createAccount(){
          let a = eth.accounts.create()
          this.newAddress = a.address;
          this.newprivatekey=a.privateKey
        }
    },
    props: {
        msg: String,
    },
};
</script>

